import React from 'react'
import { Title, Paragraph } from '../components/typography'
import { PageContent } from '../components/layout'
import { SEO } from '../components/seo'
import { Link } from 'gatsby'
import { TitleCard, Card, CardHeader, CardBody } from '../components/card'
import { LinkButton } from '../components/link'
import { ExternalLink } from '../components/link'
import { useWork } from '../hooks'
import { kebabCase } from '../util'

const WorkPage = () => {
  const work = useWork()

  return (
    <PageContent>
      <SEO 
        title="Our Work"
        description=""
        keywords=""
      />
      
      <TitleCard title="Our Work">
        <Paragraph>
          We will update this page with the latest projects coming out of RENCI as our researchers and their partners work together to develop novel approaches for dealing with COVID-19.
        </Paragraph>
        <Paragraph>
          RENCI researchers are supporting the ReCCAP UNC study to assess the effectiveness of COVID-19 preventive measures in keeping UNC workers safe as labs re-open across campus.
          The study is helping to advance research by offering free testing for COVID-19 antibodies and virus to members of UNC’s extended research community to monitor disease outbreak and reduce virus transmission.
        </Paragraph>
        <Paragraph>
          As students and workers return across the nation, it’s vital that we understand more about transmission on campus.
          This knowledge will inform best practices for testing methods and safety measures, and will help assess the usefulness of asymptomatic testing and contact tracing.
          The effort combines RENCI’s expertise in data science with UNC experts from the Depts.
          of Epidemiology, Infectious Diseases, Biology, and Microbiology and Immunology.
        </Paragraph>
        <Paragraph>
          Providing insights that make our communities safer is a key part of RENCI’s mission, and we’re even providing work space for the study as a sample collection and training site while our employees work from home.
        </Paragraph>
      </TitleCard>
      
      {
        work.map(item => (
          <Card key={ kebabCase(item.frontmatter.title) }>
            <CardHeader>{ item.frontmatter.title }</CardHeader>
            <CardBody>
              <div dangerouslySetInnerHTML={{ __html: item.frontmatter.blurb }} />
              <br/>
              <Paragraph center>
                <LinkButton to={ item.frontmatter.path }>Read More</LinkButton>
              </Paragraph>
            </CardBody>
          </Card>
        ))
      }

    </PageContent>
  )
}

export default WorkPage
